<template>
  <div id="app">
    <router-view/>
    <ToolBar v-if="showToolbars"></ToolBar>
  </div>
</template>

<script>
  import ToolBar from './views/ToolBar/index'
  export default {
    name: 'App',
    data() {
      return{
        showToolbars:false
      }
    },
    components: {
      ToolBar
    },
    created(){
      this.$http.post('http://api2.account.xinquanjk.com/access/login')
    },
    watch: {
      $route(to, from) {
        if(to.path=="/home" || to.path=="/classify" || to.path=="/car" || to.path=="/my"){
          this.showToolbars = true;
        }else{
          this.showToolbars = false;
        }
      }
    }
  }
</script>

<style>

</style>



